// –û–ø–∏—Å–∞–Ω–∏–π —É –¥–æ–∫—É–º–µ–Ω—Ç–∞—Ü—ñ—ó
import iziToast from "izitoast";
// –î–æ–¥–∞—Ç–∫–æ–≤–∏–π —ñ–º–ø–æ—Ä—Ç —Å—Ç–∏–ª—ñ–≤
import "izitoast/dist/css/iziToast.min.css";

import { fetchImages } from "./js/pixabay-api";
import { renderGallery, showLoader, hideLoader } from "./js/render-functions";

const form = document.getElementById("search-form");
const input = document.getElementById("search-input");
const gallery = document.getElementById("gallery");

form.addEventListener("submit", (event) => {
    event.preventDefault();
    const query = input.value.trim();

    // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –Ω–∞ –ø–æ—Ä–æ–∂–Ω—ñ–π –∑–∞–ø–∏—Ç
    if (!query) {
        iziToast.error({ title: "Error", message: "Please enter a search term." });
        return;
    }

    showLoader(); // –ü–æ–∫–∞–∑–∞—Ç–∏ –ª–æ–∞–¥–µ—Ä
    gallery.innerHTML = ''; // –û—á–∏—â–µ–Ω–Ω—è –≥–∞–ª–µ—Ä–µ—ó –ø–µ—Ä–µ–¥ –Ω–æ–≤–∏–º –∑–∞–ø–∏—Ç–æ–º

    fetchImages(query)
        .then(images => {
            // –Ø–∫—â–æ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –Ω–µ –∑–Ω–∞–π–¥–µ–Ω—ñ
            if (images.length === 0) {
                iziToast.warning({ message: "Sorry, there are no images matching your search query. Please try again!" });
            } else {
                renderGallery(images); // –í—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –∑–æ–±—Ä–∞–∂–µ–Ω—å
            }
        })
        .catch(() => {
            iziToast.error({ title: "Error", message: "Something went wrong. Please try again later." });
        })
        .finally(() => {
            hideLoader(); // –°—Ö–æ–≤–∞—Ç–∏ –ª–æ–∞–¥–µ—Ä
            input.value = ''; // –û—á–∏—â–µ–Ω–Ω—è –ø–æ–ª—è –≤–≤–æ–¥—É
        });
});
